@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mukta', sans-serif;
}

#root {
    display: flex;
    padding: 10px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;/* НЕ ЛОМАЕТ АДАПТАЦИЮ*/
    /*flex-direction: column;  ломает адаптацию*/
    background: black;
}

.App {
    width: 800px;
    background: black;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    border-radius: 10px;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
    color: white;

}
.post:hover {
    box-shadow: 0 0 15px rgb(153, 170, 181);
}

.post__btns {
    display: flex;
}

.remove-btn {
    margin-right: 0.5rem;
}

.post-enter {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
}
.post-enter-active {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
    transition: all 500ms ease-in;
}

.page__wrapper {
    margin-top: 10px;
    display: flex;
    color: white;
}

.page {
    padding: 10px;
    border: 1px solid teal;
    border-radius: 5px;
    cursor: pointer;
}
.page:hover {
    box-shadow: 0 0 15px rgb(153, 170, 181);
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.navbar {
    height: 50px;
    width: 100vh;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: teal;
    border-radius: 10px;
}

.navbar__links {
    margin-left: auto;
}


.link {
    padding: 10px;
    text-decoration: none;
    cursor: pointer;
    font-size: 14pt;
    color: black;
}
.link:hover {
    color: white;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 0.5s;
    font-size: 15pt;
}

.MyButtton_myBtn__2suVu {
    padding: 5px 15px;
    color: teal;
    font-size: 14px;
    background-color: transparent;
    border: 1px solid teal;
    cursor: pointer;
}

.MyButtton_myBtn__2suVu:hover {
    color: white;
    background-color: teal;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: 0.2s;
    box-shadow: 0 0 10px teal;
}
.MyModal_myModal__1pSrQ {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0, .5);
}

.MyModal_myModalContent__3qg6z {
    padding: 25px;
    background: black;
    border-radius: 16px;
    box-shadow: 0 0 10px rgb(153,170,181);
    min-width: 250px;
}

.MyModal_myModal__1pSrQ.MyModal_active__38orM {
    display: flex;
    transition: 2s; /*Скорость перехода состояния элемента*/
    -webkit-animation: MyModal_show__1hCTm 1s 1;
            animation: MyModal_show__1hCTm 1s 1; /* Указываем название анимации, её время и количество повторов*/
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    justify-content: center;
    align-items: center;
}

@-webkit-keyframes MyModal_show__1hCTm{

    0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}

@keyframes MyModal_show__1hCTm{

    0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }

}
.MyInput_myInput__3JgiO {
    width: 100%;
    padding: 5px 15px;
    margin: 5px 0;
    border: 1px solid teal;
    height: 30px;
    border-radius: 10px;
    background-color: black;
    color: white;
}

.MyInput_myInput__3JgiO:hover {
    box-shadow: 0 0 10px rgb(153,170,181);
}
.Loader_loader__2DSH7 {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__1VrEX 1s infinite linear;
            animation: Loader_rotate__1VrEX 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__1VrEX {
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}

@keyframes Loader_rotate__1VrEX {
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}
